import uniPop from '@/components/uniPop.vue';
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniPop,
		uniPopup
	},
	data() {
		return {
			banners: [],
			cartCount: '',
			specClass: 'none',
			specSelected: [],
			productInfo: {},
			member: {},
			yddMember: uni.getStorageSync("userInfo"),
			shareShow: false,
			favorite: false, //是否收藏
			posterShow: false,
			ctx: this.$ctx,
			qrcodePaths: [],
			id: null,
			shareList: [],
			imgList: [],
			inviteCode: '',
			loginCode: '',
			mobile: uni.getStorageSync(`mobile`),
			desc: ``,
			companyId: '',
			cheapPurchaseCompanyId: '',
			classification: "",
			optionType: "buy", //选择规格后是购买还是收藏
			specList: [],
			selectedSpec: {},
		};
	},
	onShareAppMessage(res) {
		return {
			title: '你是好友的',
			path: "/pages/product/product?id=" + this.id + "&inviteCode=" + this.yddMember.inviterCode + "&companyId=" + this.companyId +
				"&cheapPurchaseCompanyId=" + this.cheapPurchaseCompanyId + "&classification=" + this.classification,
			imageUrl: this.$ctx + this.productInfo.resources.smallBannerImgArr[0],
		}
	},
	onLoad(options) {
		console.log(options)
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		this.id = 347
		if (options.id) {
			this.id = options.id
		}
		this.inviteCode = options.inviteCode
		//规格 默认选中第一条
		/* this.specList.forEach(item=>{
			for(let cItem of this.specChildList){
				if(cItem.pid === item.id){
					this.$set(cItem, 'selected', true);
					this.specSelected.push(cItem);
					break; //forEach不能使用break
				}
			}
		})
		this.shareList = await this.$api.json('shareList'); */
	},
	onShow() {
		this.loadData()
		this.getCarts()
		this.login(); //微信登录
	},
	methods: {
		loadData() {
			//过去商品详情
			let data = {
				resourcesId: this.id,
				// memberId: uni.getStorageSync("userInfo").id
			}
			this.$getJson('/jsp/api/resources/resoucesInfo.jsp', data, 'POST', res => {
				console.log('----list------------', res);
				if (res.data) {
					this.productInfo = res.data.goods
					this.specList = res.data.specs
					this.selectedSpec = res.data.specs[0]
					this.banners = this.productInfo.bimgs.split(',')
					//获取店铺详情
					this.$getJson('/jsp/api/resources/resoucesInfo.jsp', data, 'POST', res => {
						
					})
				}
			});
			//店铺收藏状态
			this.$getJson('/api/v3/my/store/checkCollect.jsp', {
				storeId: 202
			}, 'POST', res => {
				this.favorite = res.data
			});
		},
		//获取购物车数量
		getCarts() {
			if (uni.getStorageSync(`mobile`)) {
				//获取购物车数量
				this.$getJson('/api/v2/vue/ydd/cart/getCartNumber.jsp', {}, 'POST', res => {
					if (res.data) {
						this.cartCount = res.data
					}
				});
			}

		},
		//分享
		shareShowBtn(e) {
			if (!uni.getStorageSync(`mobile`)) {
				this.login()
				//弹出登录
				this.$refs.popup.open()
				//防止点击遮罩关闭
				this.$refs.popup.clickClose(true)
				return
			}
			this.shareShow = !this.shareShow;
			if (e == 1) {
				this.getQrcode()
			}
			/* uni.authorize({
			    scope: 'scope.writePhotosAlbum',
			    success() {
			    },
			}) */
		},
		posterShowBtn() {
			this.posterShow = !this.posterShow;
		},
		//规格弹窗开关
		toggleSpec() {
			if (this.specClass === 'show') {
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			} else if (this.specClass === 'none') {
				this.specClass = 'show';
			}
		},
		//选择规格
		selectSpec(selectedSpec) {
			this.selectedSpec = selectedSpec
		},
		//生成小程序二维码
		getQrcode() {
			console.log('sssssss-------------', this.id + "&" + this.yddMember.inviterCode + "&" + uni.getStorageSync(
				`companyId`) + "&" + uni.getStorageSync(
				`cheapPurchaseCompanyId`) + "&" + uni.getStorageSync(`classification`));
			let data = {
				data: JSON.stringify({
					page: "pages/product/product",
					scene: this.id + "&" + this.yddMember.inviterCode + "&" + uni.getStorageSync(`companyId`) + "&" + uni.getStorageSync(
						`cheapPurchaseCompanyId`) + "&" + uni.getStorageSync(`classification`),
				})
			}
			this.$getJson('/api/v2/vue/stPlusShop/common/getQrcode.jsp', data, 'POST', res => {
				console.log('----list------------', res);
				if (res.data) {
					this.qrcodePaths = []
					this.qrcodePaths[0] = this.url + res.data
					//如果正式发布请删除
					// this.qrcodePaths[0] ='../../static/logo.jpg'
				}
			});
		},
		//点击查看大图病可以保存
		saveQrcode(e) {
			let _this = this
			if (e == 1) {
				_this.qrcodePaths = []
				this.productInfo.resources.smallBannerImgArr.forEach(item => {
					_this.qrcodePaths.push(_this.url + item)
				})
			} else if (e == 2) {
				_this.qrcodePaths = []
				this.productInfo.resources.imgArr.forEach(item => {
					_this.qrcodePaths.push(_this.url + item)
				})
			}
			uni.authorize({
				scope: 'scope.writePhotosAlbum',
				success() {
					//查看保存小程序二维码
				},
			})
			uni.previewImage({
				urls: this.qrcodePaths,
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log("errerrerr", err);
					},
					complete: function(e) {
						console.log("eeee", e)
					}
				}
			});
		},
		//收藏
		toFavorite() {
			if (!uni.getStorageSync(`mobile`)) {
				this.login()
				//弹出登录
				this.$refs.popup.open()
				//防止点击遮罩关闭
				this.$refs.popup.clickClose(true)
				return
			}
			let isCollection = 0,
				message = "取消收藏成功";
			if (this.favorite == false) {
				isCollection = 1
				message = "收藏成功"
			}
			this.$getJson('/api/v3/my/store/collect.jsp', {
				storeId: 202,
				action: isCollection
			}, 'POST', res => {
				this.favorite = !this.favorite;
				uni.showToast({
					title: message,
					icon: 'none'
				})
			});
		},
		//设置按钮action
		setOptionType(type) {
			if (true) {
				this.optionType = type
				this.toggleSpec()
			} else {
				if (type == 'buy') {
					this.buy()
				} else if (type == 'cart') {
					this.addCart()
				}
			}
		},
		//完成属性选择
		propComplete() {
			this.toggleSpec()
			if (this.optionType == 'buy') {
				this.buy()
			} else if (this.optionType == 'cart') {
				this.addCart()
			}
		},
		//购买
		buy() {
			console.log(this.productInfo)
			let goodsData = {
				hot: [],
				platform: [],
				coupon: []
			}

			if (this.productInfo.type == 1) {
				goodsData.hot.push({
					store: {},
					list: [{
						cpCart: {
							goodsCount: 1
						},
						goods: this.productInfo
					}]

				})
			} else if (this.productInfo.type == 2) {
				goodsData.platform.push({
					cpCart: {
						goodsCount: 1
					},
					goods: this.productInfo
				})
			}
			uni.setStorageSync('goodsData', goodsData)
			uni.navigateTo({
				url: '/pages/home/order/createOrder'
			})
		},
		//加入购物车
		addCart() {
			console.log('s')
			if (!uni.getStorageSync(`mobile`)) {
				this.login()
				//弹出登录
				this.$refs.popup.open()
				//防止点击遮罩关闭
				this.$refs.popup.clickClose(true)
				return
			}
			//加入购物车
			let data = {
				goodsId: this.productInfo.id,
				specId: this.selectedSpec.id,
				goodsType: this.productInfo.type
			}
			this.$getJson('/api/v2/vue/ydd/cart/addCart.jsp', {
				data: JSON.stringify(data)
			}, 'POST', res => {
				console.log('----res------------', res);
				if (res.data == 1) {
					uni.showToast({
						title: "加入成功",
						icon: 'none'
					})
					this.cartCount += 1
				} else {
					uni.showToast({
						title: res.message,
						icon: 'none'
					})
				}
			});
		},
		stopPrevent() {},

	}

}
